// IMPORTS
// Material UI
import { DialogActions, Grid, Select } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import DialogContent from '@material-ui/core/DialogContent'
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import TextField from '@material-ui/core/TextField'
import withStyles from '@material-ui/core/styles/withStyles'

// Externals imports (Dependencies)
import React from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

// Internal imports
import styles from '../../../resources/theme/stores'
import EnhancedComponent from '../../components/EnhancedComponent'

class StoreForm extends EnhancedComponent {
  state = {
    dirty: false,
    fields: {
      description: '',
      min_freight: '',
    },
  }

  handleClose = () => this.props.onClose && this.props.onClose()

  componentWillReceiveProps(nextProps) {
    if (!this.state.dirty && nextProps.data) {
      this.setState({
        fields: {
          ...nextProps.data,
        },
      })
    }
  }

  onSubmit = e => {
    e.preventDefault()
    this.props.onSubmit && this.props.onSubmit(this.state.fields)
  }

  render() {
    const { classes, roles } = this.props
    return (
      <form onSubmit={this.onSubmit}>
        <DialogContent>
          <Grid container spacing={16}>
            <Grid item>
              <TextField
                onChange={this.onChange('description')}
                label="Descrição"
                required
                fullWidth
                value={this.state.fields.description}
              />
            </Grid>
          </Grid>
          <Grid container spacing={16}>
            <Grid item>
              <TextField
                onChange={this.onChange('min_freight')}
                label="Frete Mínimo"
                required
                value={this.state.fields.min_freight}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button color="primary" type="submit">
            Salvar
          </Button>
          <Button onClick={this.handleClose}>Cancelar</Button>
        </DialogActions>
      </form>
    )
  }
}

StoreForm.propTypes = {
  classes: PropTypes.object.isRequired,
  roles: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  roles: state.store.roles || [],
})

export default connect(mapStateToProps)(withStyles(styles)(StoreForm))
